import React from 'react';
import Avatar from '@material-ui/core/Avatar';
import get from 'lodash/get';

const NameAvatarField = props => (
    <div style={{ display: 'flex', flexWrap: 'nowrap', alignItems: 'center', whiteSpace: 'nowrap', marginLeft: 6 }}>
         {
            props.record.logo ?
                <img src={props.record.logo} style={{height: 30, marginRight: 4}} />  :
            props.record.avatar ? <Avatar style={{marginRight: 8}} src={props.record.avatar} /> : null
        }
        {
            get(props.record, props.source)
        }
       
    </div>
)

export default NameAvatarField;