import { feathersClient } from './feathersClient';
import englishMessages from 'ra-language-english';
import authClientOptions from './acl/authClientOptions';
import authClient from './acl/auth';
import theme from './assets/style/theme';
import MyAppBar from './components/Layout/AppBar';
//import cnDomainMessage from './i18n/cn';
import { withStyles, createStyles } from '@material-ui/core/styles';
import globalReducer from './redux/global/reducer';
//import { Dashboard } from './containers/dashboard';
import customRoutes from './routes';
import Notification from './components/Widgets/Notification';
import Login from './components/Widgets/Login';
import { UserList, CreateUser, EditUser } from './containers/users';
import { PropList, CreateProp, EditProp } from './containers/prop';
import { SettingList, CreateSetting, EditSetting } from './containers/setting';
import {CreateCoupon,CreateArticle, CreateCampaign} from './containers/poc';
import { DynamicList, CreateDynamic,EditDynamic } from './containers/dynamic';

import * as qiniu from 'qiniu-js';

import polyglotI18nProvider from 'ra-i18n-polyglot';

const styles = theme => createStyles({
  content: {
    maxWidth: '100vw'
  },
  main: {
    display: 'flex',
    flexDirection: 'column',
    minHeight: '100vh',
    height: '1px',
    alignItems: 'center',
    justifyContent: 'flex-start',
    backgroundRepeat: 'no-repeat',
    backgroundSize: 'cover',
  },
  card: {
    minWidth: 300,
    marginTop: '8em',
    padding: '32px 16px'
  },
  avatar: {
    margin: '1em',
    display: 'flex',
    justifyContent: 'center',
  },
  icon: {
    backgroundColor: theme.palette.secondary[500],
  },
});


const i18nProvider = polyglotI18nProvider(locale => {
  // if (locale === 'en') {
  //     return messages[locale]();
  // }

  // Always fallback on Chinese
  return englishMessages;
}, 'en');


const MyLayout = props => (<Layout {...props} appBar={MyAppBar} notification={Notification} />);

const featherOptions = { id: 'id', usePatch: true };

const MyLoginPage = () => <Login />;

const dataProviderWrapper = requestHandler => async (type, resource, params) => {
  if (type === 'GET_LIST' && params.pagination && params.pagination.limit === 0) {
    return requestHandler(type, resource, params).then(d => {
      if (params.complete) {
        params.complete(d);
        return { "total": 0, "limit": 0, "skip": 0, "data": [] }
      }
    });
  } else if ((type === 'CREATE' || type === 'UPDATE') ) {
    if (params.data.props) 
    {
      let keys = Object.keys(params.data.props);
      if (keys.some(k => params.data.props[k].rawFile instanceof File )) {
         
        let token = await requestHandler('GET_LIST', 'qiniu', {
          filter: { getToken: "1"  }
        }).then(res => JSON.parse(res.data[0].id).token);
  
         keys.forEach(key => {
           if (params.data.props[key].rawFile instanceof File) {
             let file = params.data.props[key]
              let putExtra = {
                fname: '',
                params: {}, 
                mimeType: null,
              };
              let config = {
                useCdnDomain: true,
                disableStatisticsReport: false,
                retryCount: 6,
                region: qiniu.region.z0,
              };
              let subObject = {
                  next: res => {},
                  error(err){}, 
                  complete: res => console.log(res)
              };
              let d = (new Date()).getTime();
              file.src = `${process.env.REACT_APP_ASSETS_DOMAIN}/shopnow/${d}/${file.rawFile.name}`
              let observable = qiniu.upload(file.rawFile, 
                `shopnow/${d}/${file.rawFile.name}`, token, putExtra, config);
              observable.subscribe(subObject);
           }
        })
      }
    }
  }
  return requestHandler(type, resource, params);
}


const dataProvider = dataProviderWrapper(restClient(feathersClient, featherOptions));
class App extends Component {

  state = {
    loading: true
  }

  loginSucceed = data => {
 
    // if (data && window && window.Rollbar) {
    //   window.Rollbar.configure({
    //     payload: {
    //       person: {
    //         id: data.id, username: data.name
    //       }
    //     }
    //   });
    // }
    localStorage.setItem('user', JSON.stringify(data));
  }

  componentWillMount() { 
    authClientOptions.loginSucceed = this.loginSucceed;
    this.setState({ loading: false })
    feathersClient.authenticate()
      .then(data => data.user && this.loginSucceed(data.user))
      .finally(() => {
        this.setState({ loading: false })
        // dataProvider(GET_LIST, 'attributes', {
        //   pagination: { page: 1, perPage: 100000 }
        // }).then(res => localStorage.setItem('attributes',  JSON.stringify(res.data)))
    })
  }

  render = () => {
    return this.state.loading ?
      (<div className="loader-container">
        <div className="loader">Loading...</div>
      </div>) : 
      <Admin
        customReducers={{ global: globalReducer }}
        locale="en" theme={theme}
        dataProvider={dataProvider}
        // authProvider={authClient(feathersClient, authClientOptions)}
        menu={Menu} 
        loginPage={withStyles(styles)(MyLoginPage)}
        customRoutes={customRoutes}
        appLayout={withStyles(styles)(MyLayout)}
        // dashboard={Dashboard}
        i18nProvider={i18nProvider}
        messages={englishMessages}>
          <Resource name="campaigns" list={SettingList}  create={CreateCampaign} edit={EditDynamic}  /> 
          <Resource name="articles" list={SettingList}  create={CreateArticle} edit={EditDynamic} />
          <Resource name="coupons" list={SettingList}  create={CreateCoupon} edit={EditSetting}  /> 


          <Resource name="attributes" list={PropList}  create={CreateProp} edit={EditProp} /> 
          <Resource name="users" list={UserList}  create={CreateUser} edit={EditUser} /> 
          <Resource name="groups" list={DynamicList}  create={CreateDynamic} edit={EditDynamic}  /> 
          <Resource name="services" list={DynamicList}  create={CreateDynamic} edit={EditDynamic}  /> 
          <Resource name="permissions" list={DynamicList}  create={CreateDynamic} edit={EditDynamic} />
          <Resource name="api-settings" list={SettingList}  create={CreateSetting} edit={EditSetting}  /> 
      </Admin>
  }
}

export default App;
