const styles = theme => ({
    root: {
        zIndex: '1'
    },
    title: {
        flex: '1 1 auto',
        alignItems: 'flex-start'
    },
    actions: {
        //alignItems: 'flex-end'
    }
})
const ListActions = ({
    bulkActions,
    basePath,
    currentSort,
    displayedFilters,
    filters,
    filterValues,
    onUnselectItems,
    resource,
    //exporter,
    selectedIds,
    showFilter,
    total,
    exportMetadata = null,
    workingBranch,
    customActions,
    addtionalFlags
}) => ( 
        <CardActions style={{ zIndex: 0 }}>
                {customActions }
                {bulkActions && React.cloneElement(bulkActions, {
                    basePath,
                    filterValues,
                    resource,
                    selectedIds,
                    onUnselectItems,
                })}
                {filters && React.cloneElement(filters, {
                    resource,
                    showFilter,
                    displayedFilters,
                    filterValues,
                    icon: <FilterIcon />,
                    context: 'button',
                })}
                { (addtionalFlags && addtionalFlags.isHideCreate)? null
                     :
                     <Button component={Link}
                            to={`${basePath}/create`}
                            label='Create'>
                           <AddIcon />
                      </Button>
                }
                <ExportButton
                    disabled={total === 0}
                    resource={resource}
                    sort={currentSort}
                    filter={filterValues}
                    maxResults={99999}
                     
                    icon={<DownloadIcon />}
                />
                
           </CardActions>
    );

export default compose(
    withStyles(styles),
    connect(state =>  ({ workingBranch: state.global.workingBranch }))
)(ListActions);