import React, { Component } from 'react';
import { List,ArrayInput, SelectArrayInput,DeleteButton, BooleanInput,SimpleFormIterator, 
    ArrayField,SingleFieldList, ChipField,Datagrid, EditButton, TextField, Create, NumberInput,SimpleForm, 
    TextInput, SelectInput, Edit, Filter, ReferenceArrayInput } from 'react-admin';
import ListActions from '../../components/Widgets/ListActions';
import FormToolbar from '../../components/Widgets/FormToolbar';
import FormContainerActions from "../../components/Widgets/FormContainerActions";
import formCardStyle from '../../assets/style/formCard';
