import ShowFilterWhenHasOp from '../../util/ShowFilterWhenHasOp';
import CloneButton from '../../components/CloneButton';
import FromNowField from '../../components/Fields/FromNowField';


const CompoundFilter = (props) => (
    <Filter {...ShowFilterWhenHasOp(props)}>
        <SelectArrayInput label="Attribute API" source="type" choices={[
            { id: 'groups', name: 'Group' },
            { id: 'services', name: 'Service' },
            { id: 'permissions', name: 'Permission' },
            { id: 'users', name: 'User' }
        ]} />
    </Filter>
);

export const PropList = props => {
     return  <List actions={<ListActions />} filters={<CompoundFilter />} {...props}>
                <Datagrid rowClick="edit">
                    <TextField source="name" label="Attribute Name"/>
                    <ArrayField source="values" label="Attribute Value">
                        <SingleFieldList>
                            <ChipField source="key" />
                        </SingleFieldList>
                    </ArrayField>
                    <TextField source="type" label="Attribute API"/>
                    <TextField source="valueType" label="Content"/>
                    <FromNowField source="createdAt" label="Created At"/>
                    <EditButton />
                    <DeleteButton />
                </Datagrid>
            </List>
};

