import React, { Component } from 'react';
import { List, FormDataConsumer, SelectArrayInput,DeleteButton, SimpleFormIterator,useGetList, ArrayInput, ChipField,Datagrid, EditButton, TextField, Create, NumberInput,SimpleForm, TextInput, SelectInput, Edit, Filter } from 'react-admin';
import ListActions from '../../components/Widgets/ListActions';
import FormToolbar from '../../components/Widgets/FormToolbar';
import FormContainerActions from "../../components/Widgets/FormContainerActions";
import formCardStyle from '../../assets/style/formCard';
import withStyles from '@material-ui/core/styles/withStyles';
import { EditIcon, DeleteIcon } from '../../components/Icons';
import MoneyFormat from '../../util/MoneyFormat';
import ShowFilterWhenHasOp from '../../util/ShowFilterWhenHasOp';
import CloneButton from '../../components/CloneButton';
import FromNowField from '../../components/Fields/FromNowField';
import Typography from '@material-ui/core/Typography';

const CompoundFilter = (props) => (
    <Filter {...ShowFilterWhenHasOp(props)}>
        <SelectArrayInput label="Attribute API" source="type" choices={[
            { id: 'groups', name: 'Group' },
            { id: 'services', name: 'Service' },
            { id: 'permissions', name: 'Permission' },
            { id: 'users', name: 'User' }
        ]} />
    </Filter>
);

export const SettingList = props => {
     return  <List actions={<ListActions />} filters={<CompoundFilter />} {...props}>
                <Datagrid rowClick="edit">
                    <TextField source="type" label="Resource"/>
                   
                    <TextField source="form" label="Form Type"/>
                    <TextField source="variant" label="Input Variant"/>
                    <FromNowField source="createdAt" label="Created At"/>
                    <EditButton />
                    <DeleteButton />
                </Datagrid>
            </List>
};

export const CreateSetting = (withStyles)(formCardStyle)  (props => {
    const { data, loading, error } = useGetList('attributes', null, null, { type: 'groups' });
    if (loading) { return <p>LOADING</p>; }
    if (error) { return <p>ERROR</p>; }

    return (
        <Create actions={<FormContainerActions />}  title={<Typography variant="h5" >EDIT  </Typography>}  {...props}>
        <SimpleForm  toolbar={<FormToolbar />}  submitOnEnter={true} redirect="list" >

                    <SelectInput source="type" defaultValue="groups"  label="Resource" choices={[
                        { id: 'groups', name: 'Group' },
                        { id: 'services', name: 'Service' },
                        { id: 'permissions', name: 'Permission' },
                        { id: 'users', name: 'User' }
                    ]} />

                    <SelectInput source="form" defaultValue="Simple Form" label="Form Type" choices={[
                        { id: 'Simple Form', name: 'Simple Form' },
                        { id: 'Tab Form', name: 'Tab Form' }
                    ]} />

                    <SelectInput source="variant" defaultValue="outlined" label="Input Variant" choices={[
                        { id: 'outlined', name: 'Outline' },
                        { id: 'filled', name: 'Filled' },
                        { id: 'standard', name: 'Standard' }
                    ]} />
                <FormDataConsumer>
                        {({ formData, ...rest }) =>
                            {
                                console.log(data)
                                return formData.form === 'Tab Form' ? 
                                    <ArrayInput source="props" label="Tab Inforamtion">
                                        <SimpleFormIterator>
                                            <TextInput source="tab" label="Tab Name" />
                                            <SelectArrayInput label="Fields" source="fields" choices={
                                                Object.keys(data).map(key => {
                                                    console.log(data[key])
                                                return {
                                                        id: key,
                                                        name: data[key].name
                                                    }
                                                })
                                            } />
                                        </SimpleFormIterator>
                                    </ArrayInput>
                                : null
                            }
                        }
                    </FormDataConsumer>
                </SimpleForm>
        </Create>
    )
})

