import React, { Fragment } from 'react';
import { List,ArrayInput, FunctionField, DeleteButton, ReferenceArrayField, BooleanInput,ImageField,
        SimpleFormIterator,ReferenceArrayInput, ArrayField,SingleFieldList, ChipField,Datagrid,ReferenceField,
         EditButton, TextField, Create, ReferenceInput,SimpleForm, TextInput,BooleanField, RichTextField,
         ReferenceManyField, SelectInput, Edit,  useGetList, SelectField, TabbedForm, FormTab, Filter } from 'react-admin';
import ListActions from '../../components/Widgets/ListActions';
import FormToolbar from '../../components/Widgets/FormToolbar';
import FormContainerActions from "../../components/Widgets/FormContainerActions";
import formCardStyle from '../../assets/style/formCard';
import withStyles from '@material-ui/core/styles/withStyles';
import FromNowField from '../../components/Fields/FromNowField';
import {AttributeInput, InputMatch} from '../../components/Inputs/AttributeInput';
import AttributeField from '../../components/Fields/AttributeField';
import Typography from '@material-ui/core/Typography';
import AvatarField from  '../../components/Fields/AvatarField';
import get from 'lodash/get';
import NameAvatarField from  '../../components/Fields/NameAvatarField';
//import  from '../../components/Layout/Filter';

const AttributeFilter = props => {
    const { data, loading, error } = useGetList('attributes', null, null, { type: props.resource });
    return (
        <Filter {...props}>
            {
                Object.keys(data).filter(key => data[key].searchable)
                .map(key =>  InputMatch(data[key], props.variant, props.filter))
            }
        </Filter>
)}
 
const LogoField = ({ source, record = {} }) => 
   {
       const sourceValue = 'http:' + get(record, source); 
       return  <img src={sourceValue} style={{height: 50}} />
   }

export const DynamicList = props => {
    const { data, loading, error } = useGetList('attributes', null, null, { type: props.resource, list: true });
    if (loading) { return <p>LOADING</p>; }
    if (error) { return <p>ERROR</p>; }
     return  <Fragment>
          {/* <AttributeFilter resource="groups" displayedFilters={{'props.b994e9b7-21d1-462d-93ae-935ce675af81': true}} filterValues={{}} /> */}

       <List actions={<ListActions />} filters={<AttributeFilter filter={true} />} {...props}>
                <Datagrid rowClick="edit">
                    {
                        props.additional &&
                            props.additional.map(item => item)
                    }
                    {Object.keys(data)
                    .sort(key => {
                        if (data[key].name === 'Name') {
                            return -1;
                        } else {
                            return 0;
                        }
                    }).map(key => 
                       {
                           let item = data[key];
                           return item.valueType === 'mulselect'?
                            <SelectField record={props.record}
                                source={`props[${item.id}]`} label={item.name} 
                                choices={item.values.map(v => {
                                    return {
                                        id: v.key, name: v.key
                                    }
                                })} /> :
                                item.valueType === 'boolean' ?
                                <BooleanField  source={`props[${item.id}]`} label={item.name} /> :
                                item.valueType === 'richtext' ?
                                    <RichTextField source={`props[${item.id}]`} label={item.name} /> :
                                    item.valueType === 'mulreference' ?
                                    <ReferenceArrayField label={item.name} filter={{ only_name: true, resource: item.values[0].key }}
                                        source={`props[${item.id}]`} reference={item.values[0].key} >
                                        <SingleFieldList>
                                            <NameAvatarField source="name" />
                                        </SingleFieldList>
                                    </ReferenceArrayField> :
                                 item.valueType === 'image'?
                                 <ImageField style={{maxWidth: 50}} source={`props[${item.id}].src`}  label={item.name} />
                                :
                                item.valueType === 'logo'?
                                 <LogoField label={item.name} source={`props.${item.id}.src`}  />
                                :
                                item.valueType === 'avatar'?
                                <AvatarField label={item.name} source={`props.${item.id}.src`}  />
                               :
                                item.valueType === 'sinreference'?
                                <ReferenceField filter={{ only_name: true, resource: item.values[0].key }}
                                    source={`props[${item.id}].src`} label={item.name} reference={item.values[0].key}>
                                    <NameAvatarField source="name" />
                                </ReferenceField>
                               :
                                    item.name === 'Name' ?
                                    <NameAvatarField source={`props.${item.id}`} label={item.name} /> 
                                    :
                                    <TextField source={`props.${item.id}`} label={item.name} />
                        })
                    }
                    <FromNowField source="createdAt" label="Created At"/>
                    <EditButton />
                    <DeleteButton />
                </Datagrid>
            </List></Fragment>
};

export const CreateDynamic = (withStyles)(formCardStyle) (props => {
    const { data, loading, error } = useGetList('attributes', null, null, { type: props.resource });
    const settings = useGetList('api-settings', null, null, { type: props.resource }); 
    if (loading) { return <p>LOADING</p>; }
    if (error) { return <p>ERROR</p>; }

    return (
        <Create actions={<FormContainerActions />} title={<Typography variant="h5" >CREATE  </Typography>}  {...props}>
           
            {
               settings.total > 0 && settings.data[settings.ids[0]].form === 'Tab Form' &&
               settings.data[settings.ids[0]].props.length > 0 ?
                <TabbedForm  variant={settings.total > 0 ? settings.data[settings.ids[0]].variant : 'outlined'} 
                     toolbar={<FormToolbar />} submitOnEnter={true} redirect={
                   props.inDrawer ? '' : 'list' } >
                    {

                        settings.data[settings.ids[0]].props.map(tab => 
                                    <FormTab label={tab.tab}>
                                        <AttributeInput variant={settings.total > 0 ? settings.data[settings.ids[0]].variant : 'outlined'} 
                                            choices={Object.keys(data).filter(key => tab.fields.some(f => f === key))
                                                    .map(key => data[key])}>  
                                        </AttributeInput>
                                    </FormTab>
                            )
                    }
                </TabbedForm> :
                <SimpleForm variant={settings.total > 0 ? settings.data[settings.ids[0]].variant : 'outlined'} 
                toolbar={<FormToolbar />} submitOnEnter={true} redirect={
                    props.inDrawer ? '' : 'list' } >
                     {
                            props.additional &&
                                props.additional.map(item => React.cloneElement(item, { variant: settings.total > 0 ? settings.data[settings.ids[0]].variant : 'outlined'} ))
                    }
                    {
                        <AttributeInput variant={settings.total > 0 ? settings.data[settings.ids[0]].variant : 'outlined'} 
                            choices={Object.keys(data).map(key => data[key])}>  </AttributeInput>
                    }

                </SimpleForm>
            }
          
        </Create>
    )
})

