import React, { Fragment} from 'react'
import PropTypes from 'prop-types';
import { useInput, required, BooleanInput, SelectInput, TextInput,ReferenceArrayInput,SelectArrayInput,RadioButtonGroupInput
         ,ReferenceInput, AutocompleteInput, ImageInput, regex,ImageField, CheckboxGroupInput, NumberInput } from 'react-admin';
import Select from '@material-ui/core/Select';
import DomainInput from './DomainInput';
//import Select from './DomainInput';
import RichTextInput from 'ra-input-rich-text';
import DrawerInput from '../../components/Inputs/DrawerInput';
import { CreateDynamic } from '../../containers/dynamic';
import AvatarField from '../Fields/AvatarField';
import MenuItem from '@material-ui/core/MenuItem';
import Avatar from '@material-ui/core/Avatar';

const InputMatch = (item, variant = 'standard', filter=false) => 
            item.valueType === 'mulselect)'?
                    <SelectArrayInput options={{variant : variant  }}
                        source={`props.${item.id}`} label={item.name} 
                        choices={item.values.map(v => {
                            return {
                                id: v.key, name: v.key
                            }
                        })} /> :
                        item.valueType === 'sinselect'?
                    <SelectInput options={{variant : variant}}
                        source={`props.${item.id}`} label={item.name} 
                        choices={item.values.map(v => {
                            return {
                                id: v.key, name: v.key
                            }
                        })} /> :
                        item.valueType === 'boolean' ?
                    <BooleanInput variant={variant} source={`props.${item.id}`} label={item.name} /> :
                    item.valueType === 'domain' ?
                    <DomainInput variant={variant} source={`props.${item.id}`} label={item.name} /> :
                    item.valueType === 'email' ?
                    <TextInput variant={variant} validate={regex(/^[a-zA-Z0-9.!#$%&*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/, 'Email is invalid')} source={`props.${item.id}`} label={item.name} type="email" />:
                    item.valueType === 'checkbox' ?
                    <CheckboxGroupInput variant={variant} source={`props.${item.id}`} 
                    choices={item.values.map(v => {
                        return { id: v.key, name: v.value }
                    })} /> :
                    item.valueType === 'radiobutton' ?
                    <RadioButtonGroupInput variant={variant} source={`props.${item.id}`} 
                        choices={item.values.map(v => {
                            return { id: v.key, name: v.value }
                        })} /> :
                    item.valueType === 'number' ?
                        <NumberInput variant={variant} source={`props.${item.id}`} label={item.name} /> :
                    item.valueType === 'money' ?
                        <NumberInput variant={variant} source={`props.${item.id}`} label={item.name} /> :
                    item.valueType === 'richtext' ?
                        <RichTextInput options={{variant : variant }} variant={variant} source={`props.${item.id}`} label={item.name} /> :
                    item.valueType === 'mulreference' ?
                         !filter ?
                            <DrawerInput variant={variant} input={
                                <ReferenceArrayInput  validate={[ !item.nullable ? required() : false]}
                                filter={{ only_name: true, resource: item.values[0].key }}
                                label={item.name} source={`props.${item.id}`} reference={item.values[0].key}>
                                     <SelectArrayInput optionText={choice => {
                                                                return <div style={{display: 'flex', alignItems: 'center'}}>
                                                                    {
                                                                        choice.avatar &&
                                                                        <Avatar style={{width: 24, height: 24, marginRight: 8}} src={choice.avatar}></Avatar> 
                                                                    }
                                                                   { choice.name}
                                                                </div>
                                         }} options={{variant : variant, fullWidth: true }}/>
                                    </ReferenceArrayInput>}
                                drawerContent={<CreateDynamic
                                                    variant={variant}
                                                    inDrawer={true}
                                                    basePath={`${item.values[0].key}`}
                                                    resource= {`${item.values[0].key}`}/>}  /> :
                              <ReferenceArrayInput  validate={[ !item.nullable ? required() : false]}
                                                    filter={{ only_name: true, resource: item.values[0].key }} 
                                                    label={item.name} source={`props.${item.id}`} reference={item.values[0].key}>
                                                        <SelectArrayInput variant={variant}
                                                            optionText={choice => {
                                                                return <div style={{display: 'flex', alignItems: 'center'}}>
                                                                    {
                                                                        choice.avatar &&
                                                                        <Avatar style={{width: 24, height: 24, marginRight: 8}} src={choice.avatar}></Avatar> 
                                                                    }
                                                                   { choice.name}
                                                                </div>
                                                            }} options={{variant : variant  }}/>
                                                        </ReferenceArrayInput>                    
                    :
                    item.valueType === 'sinreference' ?
                    <ReferenceInput variant={variant} filter={{ only_name: true, resource: item.values[0].key }}
                        validate={[ !item.nullable && !filter ? required() : false]}
                        label={item.name} source={`props.${item.id}`} reference={item.values[0].key} >
                        <SelectInput options={{variant : variant  }} optionText="name"  />
                    </ReferenceInput>
                    :
                    item.valueType === 'image' || item.valueType === 'logo' || item.valueType === 'avatar'?
                    <ImageInput variant={variant} source={`props.${item.id}`} label={item.name}  accept="image/*">
                        {
                            item.valueType === 'image' || item.valueType === 'logo' ?
                            <ImageField style={{width: 400}} source="src" title="title" />
                            :
                            <AvatarField source="src"/>
                        }
                    </ImageInput> :
                    
                    <TextInput variant={variant}
                     source={`props.${item.id}`} validate={[
                        !item.nullable && !filter ? required() : false
                    ]} label={item.name} /> 

const AttributeInput = props => {
    //const { input, meta: { touched, error } } = useInput(props);
    console.log(props.variant)
    return (
        props.choices
        .sort(c => {
            if (c.name === 'Name') {
                return -1;
            } else {
                return 0;
            }
        })
        .map(item => 
            <div style={{margin: '20px 0px'}}>
                {
                    InputMatch(item, props.variant)
                }
            </div>
        )
    );
}

export {AttributeInput, InputMatch}
  