import React from 'react';
import Avatar from '@material-ui/core/Avatar';
import get from 'lodash/get';

const AvatarField = ({ record, size, source }) => {
     var objurl = record.rawFile && record.rawFile instanceof File ?
      window.URL.createObjectURL(new Blob([record.rawFile])) : get(record, source); 
    return  (
        <Avatar
            src={objurl}
            size={50}
            style={{ width: 50, height: 50 }}
        />
    )
}

AvatarField.defaultProps = {
    size: 25,
};

export default AvatarField;