
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import compose from 'recompose/compose';
import SettingsIcon from '@material-ui/icons/Settings';
import { withRouter } from 'react-router-dom';
import {
    translate,
    MenuItemLink,
    Responsive,
} from 'react-admin';
import { withStyles } from '@material-ui/core/styles';
import SubMenu from './SubMenu';
import {
    WechatIcon, ApartmentIcon, EmployeeIcon, SettingIcon,
    ServiceIcon, PermissonIcon, CampaignIcon
} from '../Icons';

const styles = theme => ({
    active: {
        color: theme.palette.primary.main,
        fontWeight: 900
    }
});

class Menu extends Component {
    state = {
        menus: {}
    };

    static propTypes = {
        onMenuClick: PropTypes.func,
        logout: PropTypes.object,
    };

    handleToggle = menu => {
        if (this.state.menus.hasOwnProperty(menu)) {
            this.state.menus[menu] = !this.state.menus[menu];
        } else {
            this.state.menus[menu] = true;
        }
        this.setState(this.state.menus);
        if (localStorage) {
            localStorage.setItem('selectedMenu', JSON.stringify(this.state.menus));
        }
    };

    componentWillMount() {
        if (localStorage &&
            Object.entries(this.state.menus).length === 0) {
            let menus = localStorage.getItem('selectedMenu');
            if (menus) {
                try {
                    this.setState({ menus: JSON.parse(menus) })
                }
                catch (err) { }
            }
        }
    }

    render() {
        const { onMenuClick, open, logout, translate, theme, classes } = this.props;
        return (
            <div>
                {/* <MenuItemLink
                    to="/" onClick={onMenuClick}
                    primaryText={translate('ra.page.dashboard')}
                    leftIcon={<DashboardIcon />} exact
                /> */}
                 <SubMenu  handleToggle={() => this.handleToggle('menuCampaign')}
                            isOpen={this.state.menus['menuCampaign']}
                            sidebarIsOpen={open} icon={<CampaignIcon/>}
                            name="Marketing" >
                            <MenuItemLink
                                to="/campaigns" onClick={onMenuClick}
                                primaryText={translate('Campaigns')}
                                exact
                            />
                </SubMenu>
                <SubMenu  handleToggle={() => this.handleToggle('menuMarketing')}
                            isOpen={this.state.menus['menuMarketing']}
                            sidebarIsOpen={open} icon={<WechatIcon/>}
                            name="Wechat" >
                            <MenuItemLink
                                to="/articles" onClick={onMenuClick}
                                primaryText={translate('Articles')}
                                exact
                            />
                            <MenuItemLink
                                to="/coupons" onClick={onMenuClick}
                                primaryText={translate('Coupons')}
                                exact
                            />
                </SubMenu>
                <SubMenu handleToggle={() => this.handleToggle('menuACL')}
                            isOpen={this.state.menus['menuACL']}
                            sidebarIsOpen={open} icon={<SettingIcon/>}
                            name="Access Control" >
                            <MenuItemLink
                                to="/groups" onClick={onMenuClick}
                                primaryText={translate('Groups')}
                                leftIcon={<ApartmentIcon />} exact
                            />

                            <MenuItemLink
                                to="/services" onClick={onMenuClick}
                                primaryText={translate('Services')}
                                leftIcon={<ServiceIcon />} exact
                            />

                            <MenuItemLink
                                to="/users" onClick={onMenuClick}
                                primaryText={translate('Users')}
                                leftIcon={<EmployeeIcon />} exact
                            />
                            <MenuItemLink
                                to="/permissions" onClick={onMenuClick}
                                primaryText={translate('Permissions')}
                                leftIcon={<PermissonIcon />} exact
                            />

                            <MenuItemLink
                                to="/attributes" onClick={onMenuClick}
                                primaryText={translate('Attributes')}
                                leftIcon={<SettingIcon />} exact
                            />

                            <MenuItemLink
                                to="/api-settings" onClick={onMenuClick}
                                primaryText={translate('Resource')}
                                leftIcon={<SettingIcon />} exact
                            />
                        </SubMenu>
                
                
                <Responsive
                    xsmall={
                        <MenuItemLink
                            to="/configuration"
                            primaryText={translate('pos.configuration')}
                            leftIcon={<SettingsIcon />}
                            onClick={onMenuClick}
                        />
                    }
                    medium={null}
                />


                


                <Responsive
                    small={logout}
                    medium={null} // Pass null to render nothing on larger devices
                />
            </div>
        );
    }
}

const mapStateToProps = state => ({
    open: state.admin.ui.sidebarOpen,
    workingBranch: state.global.workingBranch,
    user: state.global.user,
    locale: state.i18n ? state.i18n.locale : 'zh',
});

const enhance = compose(
    withRouter,
    connect(
        mapStateToProps,
        {}
    ),
    translate,
    withStyles(styles)
);

export default enhance(Menu);
