export const CreateCoupon = (withStyles)(formCardStyle)  (props => {
    
    const [checked, setChecked] = React.useState({
        checked: false,
      });

      const classes = useStyles();
      const theme = useTheme();
      const [personName, setPersonName] = React.useState(departments);
    
      const handleChange = event => {
        setPersonName(event.target.value);
      };
    
      const handleChangeMultiple = event => {
        const { options } = event.target;
        const value = [];
        for (let i = 0, l = options.length; i < l; i += 1) {
          if (options[i].selected) {
            value.push(options[i].value);
          }
        }
        setPersonName(value);
      };

    return (
        <Create actions={<FormContainerActions />}  title={<Typography variant="h5" >Create Coupon  </Typography>}  {...props}>
           <TabbedForm toolbar={<FormToolbar />} submitOnEnter={true} >
                    <FormTab label="Basic Info">
                        <div>
                            <img src={c} />
                        </div>                         
                    </FormTab>

                    <FormTab label="Coupon Code">
                        <FormControlLabel
                            control={
                                <Switch style={{width: 50}}
                                    checked={checked}
                                    onChange={() => setChecked(!checked)}
                                    value="checked"  />
                            }  label="Custom Code" />
                        
                            {
                                checked && <Button variant="contained">Upload Code</Button>
                            }
                      </FormTab>

                      <FormTab label="ACL">
                      <FormControl className={classes.formControl}>
                            <InputLabel id="demo-mutiple-chip-label">Can Access Departments</InputLabel>
                                <Select
                                    labelId="demo-mutiple-chip-label"
                                    id="demo-mutiple-chip"
                                    multiple
                                    value={personName}
                                    onChange={handleChange}
                                    input={<Input id="select-multiple-chip" />}
                                    renderValue={selected => (
                                        <div className={classes.chips}>
                                        {selected.map(value => (
                                            <Chip key={value} label={value} className={classes.chip} />
                                        ))}
                                        </div>
                                    )}
                                    MenuProps={MenuProps}
                                    >
                                    {names.map(name => (
                                        <MenuItem key={name} value={name} style={getStyles(name, personName, theme)}>
                                        {name}
                                        </MenuItem>
                                    ))}
                                </Select>
                        </FormControl>     
                      </FormTab>
                </TabbedForm> 
        </Create>
    )
})


