export const CreateCampaign = (withStyles)(formCardStyle)  (props => {
    
    const classes = useStyles();
    const theme = useTheme();
    const [personName, setPersonName] = React.useState([]);
  
    const handleChange = event => {
      setPersonName(event.target.value);
    };
  
    const handleChangeMultiple = event => {
      const { options } = event.target;
      const value = [];
      for (let i = 0, l = options.length; i < l; i += 1) {
        if (options[i].selected) {
          value.push(options[i].value);
        }
      }
      setPersonName(value);
    };
    return ( 
        <Create actions={<FormContainerActions />} {...props}
             title={<Typography variant="h5" >Create Campaign  </Typography>} >
           <TabbedForm toolbar={<FormToolbar />} submitOnEnter={true} >
                    <FormTab label="To">
                        <TextField
                            select variant="outlined"
                            label="Audience"
                            style={{marginTop: 16}}
                            value="all"
                            helperText="Please select your audience" >
                            <MenuItem key="all" value="all">
                                 All
                                </MenuItem>
                            </TextField>
                    </FormTab>
                    <FormTab label="From">
                    <TextField variant="outlined"
                            label="To"
                            style={{marginTop: 16}}>
                          
                            </TextField>
                    </FormTab>
                    <FormTab label="Cotent">
                        <FormControl className={classes.formControl}>
                            <InputLabel id="demo-mutiple-chip-label">Articles</InputLabel>
                                <Select
                                    labelId="demo-mutiple-chip-label"
                                    id="demo-mutiple-chip"
                                    multiple
                                    value={personName}
                                    onChange={handleChange}
                                    input={<Input id="select-multiple-chip" />}
                                    renderValue={selected => (
                                        <div className={classes.chips}>
                                        {selected.map(value => (
                                            <Chip key={value} label={value} className={classes.chip} />
                                        ))}
                                        </div>
                                    )}
                                    MenuProps={MenuProps}
                                    >
                                    {names.map(name => (
                                        <MenuItem key={name} value={name} style={getStyles(name, personName, theme)}>
                                        {name}
                                        </MenuItem>
                                    ))}
                                </Select>
                        </FormControl>                     
                    </FormTab>
                </TabbedForm> 
        </Create>
    )
})