import TextField from '@material-ui/core/TextField';
import { useInput, required, regex , TextInput} from 'react-admin';
import React, { Component} from 'react'

const validateDomain = regex(/(?:[a-z0-9](?:[a-z0-9-]{0,61}[a-z0-9])?\.)+[a-z0-9][a-z0-9-]{0,61}[a-z0-9]/, 'Domain is invalid');

 export default props => (
    <span>
        <TextInput source={props.source} validate={validateDomain} label={props.label}  />
    </span>
);