import React from 'react';
import Typography from '@material-ui/core/Typography';
import Tooltip from '@material-ui/core/Tooltip';

const moment = require('moment');

const FromNowField = props =>  (
    props.record && props.record[props.source] ? 
    <Tooltip title={moment(props.record[props.source]).fromNow()}>
        <Typography component="body1" > 
           {
               //(new Date(props.record[props.source])).toLocaleString('en-US', { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric', hour: 'numeric', minute: 'numeric', hour12: false, timeZone: 'Asia/Shanghai' })
               moment(new Date(props.record[props.source])).fromNow()
           }
        </Typography>
    </Tooltip> : null
)

export default FromNowField;